import Vue from 'vue';
import confirm from './index.vue';
// 

let confirmConstructor = Vue.extend(confirm);

let MyConfirm = function (text) {
    return new Promise((res, rej) => { //promiseokresolvenorejectlet
      let confirmDom = new confirmConstructor({   
        el: document.createElement('div')
      })
      document.body.appendChild(confirmDom.$el);  //newbody

      confirmDom.text = text;   //confirm
      confirmDom.ok = function () {
        res()
        this.top = 140
        this.opacity = 0
        setTimeout(() => {
          this.isShow = false
        }, 300);
      }
      confirmDom.close = function () {
        rej()
        this.top = 140
        this.opacity = 0
        setTimeout(() => {
          this.isShow = false
        }, 300);
      }

    })
}

  export default MyConfirm;  
  
 
