<template>
  <div class="ElectronicsBox">
    <p class="Ele" 
        :style="{ 
            color: Numcolor, 
            background: BGcolor , 
            fontSize: fontSize + 'px'
        }" >
        {{displayNumber}}
    </p>
  </div>
</template>
<script>
export default {
  name: 'NumAnimation',
// :EleNumber     
// :fontSize      
// :Numcolor      
// :BGcolor            none
// :Numspeed                100
// :Numfrequency              20
  props:{
        EleNumber:{
            type:Number,
            default:9999
        },
        BGcolor:{
            type:String,
            default:'none'
        },
        Numcolor:{
            type:String,
            default:'#000'
        },
        fontSize:{
            type:Number,
            default:20
        },
        Numspeed:{
            type:Number,
            default:100
        },
        Numfrequency:{    
            type:Number,
            default:20
        }
  },
  data(){
    return{
        MyEleNumber:this.EleNumber,
        displayNumber:0,
        numTime:null
    }
  },
  created(){},
  mounted(){
      this.animationNum()
  },
  computed: {},
  watch:{},  
  components:{},
  methods:{
      animationNum(){
          // 1
          var pace = 1                  
          //Numfrequency
          if(this.MyEleNumber > this.Numfrequency){     //20     100 --> 5 
              pace = parseInt(this.MyEleNumber / this.Numfrequency)   //
          }
          this.numTime = setInterval(()=>{
              this.displayNumber += pace    //
              if(this.displayNumber >= this.MyEleNumber  ){    //
                  clearInterval(this.numTime)
                  this.numTime = null                        //
                  this.displayNumber = this.MyEleNumber      //
              }
          },this.Numspeed)
      }
  }
}
</script>
<style>
/* main.js */
.Ele{
    text-align: center;
    font-family: ele;
    /*  */
    letter-spacing:7px;
    margin: 10px 0;
    /* font-size: 60px; */
     /* color */
    /* color: #00ffff; */
    /* -webkit-text-fill-color: #00ffff; */
    /*  */
    /* -webkit-text-stroke: 1px #000000;   */
}
.ElectronicsBox{
  width: 100%;
  height: 100%;
}
</style>

