import scrollContainer from './scrollContainer/scrollContainer.vue'
import Button from './button/index.vue'
import NumAnimation from './numAnimation/index.vue'
import Pagination from './Pagination/index.vue'


//   
const SupTopUI = {};
const componentPool = [
    scrollContainer,
    Button,
    NumAnimation,
    Pagination
]
//  options main.js
SupTopUI.install = function(Vue,options){
    if(options && options.components){    //options
        const components = options.components
        components.forEach((componentsName) => {
            componentPool.forEach((component)=>{
                if(componentsName == component.name){
                    Vue.component(component.name , component);
                }
            })
        })
    }else{                            //options
        componentPool.forEach(component => {
            Vue.component(component.name , component);
        });
    }
}
export default SupTopUI