<template>
  <!--  -->
  <transition name="tips_tran">
    <div class="tips"  :class="config.type" >
      <div class="tips_context"  :style="{top:top+'px'}">
        <p class="tips_tips" v-if="config.tit">{{ config.tit }}</p>
        <p class="tips_msg">{{ config.msg }}</p>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
    name:'tips',
  data() {
    return {
      top:-100,
      config: {
        type:'warning ',
        tit: "",
        msg: "",
        top:20,
        time:2000
      },
    };
  },
  mounted(){
      this.animation()
  },
  methods: {
    animation(){
        // top
        setTimeout(() => {
            //  20px
            this.top = this.config.top >= 20 ? this.config.top : 20
        }, 0);
        // 2
        setTimeout(() => {
            this.top = -100
        }, this.config.time);
    }
  },
};
</script>
<style>
/*  */
.tips_context {
  width: 300px;
  color: #909399;
  background: #e9e9eb;
  position: fixed;
  left: 50%;
  transform: translate(-50%);
  border-radius: 5px;
  padding: 5px;
  z-index: 999;
  box-sizing: border-box;
  transition: .5s;
}
.success .tips_context{
    background: #e1f3d8;
    color: #67c23a;
}
.warning .tips_context{
    background: #faecd8;
    color: #e6a23c;
}
.error .tips_context{
    background: #fde2e2;
    color: #f56c6c;
}
.default .tips_context{
    background: #d6f0ff;
    color: #00a2ff;
}
.tips_tips {
  text-align: center;
  line-height: 20px;
  font-size: 18px;
  font-weight: 700;
}
.tips_msg {
  margin: 10px 0;
  line-height: 20px;
  text-align: center;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}

</style>
