import Vue from 'vue';
import tips from './index.vue';
// 
let confirmConstructor = Vue.extend(tips);
let MyTips = function (text) {
      let tipsDom = new confirmConstructor().$mount()
      document.body.appendChild(tipsDom.$el);  //newbody
      tipsDom.config = text;   //confirm   
}
export default MyTips;  
  
