<template>
  <div class="buttonBox"
    :class="{
        info : type == 'info',
        success : type == 'success',
        warning : type == 'warning',
        error : type == 'error',
        default : type == 'default',
        fl : float == 'l',
        fr : float == 'r'
    }"
    :style="{ 
            width: Width, 
            background: BGcolor, 
            color:FontColor,
            lineHeight:lineHeight + 'px',
            fontSize: fontSize + 'px'
        }" 
        @click="Myclick"
    >
     {{butContent}}
  </div>
</template>
<script>
            // success/
            // warning/
            // info/
            // error  
export default {
  name: 'Button',
  props:{
        Width:{        //
            type:String,
            default:'auto'
        },
        BGcolor:{       //
            type:String,
            default:''
        },
        FontColor:{     //
            type:String,
            default:'#fff'
        },
        fontSize:{      //
            type:Number,
            default:14
        },
        lineHeight:{
            type:Number,
            default:30
        },
        butContent:{     //
            type:String,
            default:''
        },
        type:{
            type:String,
            default:'default'
        },
        float:{
            type:String,
            default:''
        }
  },
  data(){
    return{
     
    }
  },

  methods:{
      Myclick(){
          this.$emit('click', this.type)
      }
  }
}
</script>
<style>
.buttonBox{
    cursor: pointer;
    padding: 0 20px;
    box-sizing: border-box;
    opacity: 0.8;
    text-align: center;
    transition: 0.3s;
    border-radius: 5px;
    margin: 0 auto;
    -moz-user-select: none; /**/
    -webkit-user-select: none; /*webkit*/
    -ms-user-select: none; /*IE10*/
    -khtml-user-select: none; /**/
    user-select: none;
}
.fl.buttonBox{
    float: left;
    margin: 2px 5px;
}
.fr.buttonBox{
    float: right;
    margin: 2px 5px;
}
.buttonBox:hover{
    opacity: 1;
}
.default.buttonBox{
    background: #00a2ff;
}
.info.buttonBox{
    background: #909399;
}
.success.buttonBox{
    background: #67c23a;
}
.warning.buttonBox{
    background: #e6a23c;
}
.error.buttonBox{
    background: #f56c6c;
}
</style>

